type VALUESUSEDOWNLOAD = {
  url: string;
  name: string;
};

export const useDownloadFile = (values: VALUESUSEDOWNLOAD) => {
  const { url, name } = values;
  const a = window?.document?.createElement(`a`);
  a.href = url || ``;
  a.target = `_blank`;
  a.download = name;
  document?.body?.appendChild(a);
  a.click();
  document?.body?.removeChild(a);
};
